import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

const routes: Routes = [{
    path: '',
    children: [
        {
            path: 'page-preserve',
            loadChildren: "./page-preserve/page-preserve.module#PagePreserveModule",
        },//页面维护
        {
            path: 'page-data-set',
            loadChildren: "./page-data-set/page-data-set.module#PageDataSetModule",
        },//页面集维护
        {
            path: 'business-scence',
            loadChildren: "./business-scence/business-scence.module#BusinessScenceModule",
        },//业务场景
        {
            path: 'page-control',
            loadChildren: './page-control/page-control.module#PageControlModule',
        }

    ]
}]

export const PageLayoutRouting: ModuleWithProviders = RouterModule.forChild(routes);